import {mapMutations } from 'vuex';
// 
export default {
    methods : {
        ...mapMutations([
            'setPageYOffset'
        ]),
        toPage(id) {
            this.setPageYOffset(document.documentElement.scrollTop);
            this.$router.push({name : 'dishDetail', params:{pid:id}});
        }
    }
}


