import Cark from '@/page/compon/cark';
export default {
    components : {
        Cark
    },
    data() {
        return {
            popupVisible : false,   // 
            data : null,    // 
            moreUnit : null,    // 
            indexUnit : 0   // 
        }
    },
    methods: {
        showPop(data) {
            this.popupVisible = !this.popupVisible;
            data.forEach(element => {
                element.oss = 0;
                element.is_multi_type = 0;
            });
            this.data = data;
            this.moreUnit = data[0];
            this.indexUnit = 0;
        },
        changeIndex(index) {
            this.indexUnit = index;
            this.moreUnit = this.data[index];
        },
        closePop() {
            this.popupVisible = false;
        }
    },
}


